<template>
  <div  >
      <div class="block" style="float:left;width:500px;margin-top:150px;margin-left:100px;">
    <el-carousel height="350px" :interval="2000">
      <el-carousel-item v-for="item in imagebox" :key="item.id">
             <img :src="item.idView" class="image">
          </el-carousel-item>
    </el-carousel>
  </div>



<div  style="float:right;width:450px;margin-top:200px;margin-right:150px; "  >
   
    
      <!---->
       <el-form ref="FormRef" :model="user_info" :rules="Rules" class="login_form" label-width="100px" >
        <el-form-item label="" prop="user_id">
          <el-input v-model="user_info.user_id" prefix-icon="iconfont icondenglu" style="width:450px;"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="user_info.password" prefix-icon="iconfont iconmima" style="width:450px;"></el-input>
        </el-form-item>
        <el-form-item label="" prop="querenpassword">
          <el-input  v-model="user_info.querenpassword" prefix-icon="iconfont iconmima" style="width:450px;"></el-input>
        </el-form-item>
        
          <el-form-item label="" prop='phone'>
          <el-input v-model='user_info.phone'  style="width:450px;"></el-input>
        </el-form-item>
        <el-form-item class="btns">
          <el-button type="success" @click="register" plain></el-button>
          <el-button type="info" @click="resetForm" plain></el-button>
        </el-form-item>
      </el-form>
        
     
    </div>
    
  


  </div>
</template>

<script>
export default {
  
  data() {
    var validatePass2 = (rule, value, callback) => {
         console.log(value);
          if (this.user_info.querenpassword === '') {
              callback(new Error(''));
          } else if (this.user_info.querenpassword != this.user_info.password) {
              callback(new Error('!'));
          } else {
              callback();
          }
      };
    return {
        imagebox:[{id:0,idView:require('../assets/image/1.jpg')},
         {id:1,idView:require('../assets/image/2.jpg')},
        {id:2,idView:require('../assets/image/3.jpg')}
         ],
     
     
     
       user_info: {
        user_id: "",
        password: "",
        querenpassword:"",
        type:"",
        phone:"",
      },
     
     
      Rules:{
       user_id:[
         { required: true, message: '', trigger: 'blur' },//
         { min: 5, max: 12, message: ' 5  12 ', trigger: 'blur' } //
       ],
       password:[
         { required: true, message: '', trigger: 'blur' },//
         { min: 6, max: 10, message: ' 6  10 ', trigger: 'blur' } //

       ],
        querenpassword:[
         //{ required: true, message: '', trigger: 'blur' },//
         //{ min: 6, max: 10, message: ' 6  10 ', trigger: 'blur' }, //
         { validator: validatePass2, trigger: 'blur', required: true },
       ],
        
            
      }, 

    };
  },

  methods:{
   
   resetForm(){
      this.$refs.FormRef.resetFields();
    },


    register(){
     this.$refs.FormRef.validate(async valid =>{
            if(!valid) return;
            const {data:res} = await this.$http.post("Register",this.user_info);
            console.log(res,res=="ok");
            if(res=="ok"){
                   
                    this.$message.success("");              
                           
                                
            }else{
                   this.$message.error("");
            }
      })
   },
   
  },
};
</script>

<style lang="less" scoped>

// 
.login_container {
  background-color: #fff;
  height: 100%;
}
// 
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;// 
  position: absolute;// 
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);//  
  border: 1px solid #fff;
 // 
  .avatar_box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%; // 
    padding: 10px;
    box-shadow: 0 0 10px #ddd;// 
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #0ee;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%; // 
      background-color: #eee;
    }
  }
  .btns {
    display: flex;// 
    justify-content: flex-end; // 
  }
  .login_form {
    position: absolute;
    bottom: 0%;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;// 
  }
}

.el-carousel__item h3 {
      color: #475669;
      font-size: 14px;
      opacity: 0.75;
      line-height: 200px;
      margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
      background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
      background-color: #d3dce6;
    }
    img{
     /**/
      width:100%;
      height:inherit;
    }
</style>